import React, { Component } from 'react';
import Weather from './components/Weather';
import EmailInput from './components/EmailInput';
import Http from './services/httpService';
import { ClipLoader } from 'react-spinners';
import RandomGiphy from './components/RandomGiphy';
import config from './conifg';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      daily: null,
      today: null,
      loading: true,
      backup: false,
      error: false,
      rain: {}
    };
  }

  componentDidMount(){
    this.setState({
      loading: true
    })
    const promise1 = new Promise((resolve, reject) => {
      Http.get('forecast')
      .then(({ daily, today, rain }) => {
        setTimeout(() => this.setState({
          daily,
          today,
          rain,
          loading: false
        }), 1000);
      })
    });

    const promise2 = new Promise((resolve, reject) => {
      setTimeout(() => {
        this.handleBackupRequests();
        resolve();
      }, 4000, 'two');
    });

    Promise.race([promise1, promise2]).then((value) => {
      console.log(value);
    });
  }

  handleBackupRequests = () => {
    var daily = new Promise((resolve, reject) => {
      Http.get(`forecast/daily?lang=ro&lat=46.909738&lon=23.807249&units=metric&APPID=${config.key}`, true)
      .then((data) => {
        this.setState({
          daily: data
        })
        resolve(data);
      })
    });

    var today = new Promise((resolve, reject) => {
      Http.get(`weather?lang=ro&lat=46.909738&lon=23.807249&units=metric&APPID=${config.key}`, true)
      .then((data) => {
        this.setState({
          today: data
        })
        resolve(data);
      })
    });

    Promise.all([today, daily]).then((values) => {
      const isItRainingNow = values[0].weather[0].id < 700;
      const rain = {
        isItRainingNow: isItRainingNow
      }
      this.setState({
        rain,
        backup: true,
        loading: false
      })
    })
    .catch(() => {
      this.setState({
        error: true,
        loading: false
      });
    });
  }

  renderLoading = () => {
    return (
      <div className="loading">
        <ClipLoader
          size={60}
          color={'rgb(254, 226, 52)'}
          loading={this.state.loading}
        />
      </div>
    )
  }

  render() {
    const { today, daily, loading, rain, error, backup } = this.state;
    return (
      <div className="App">
        {
          loading ? this.renderLoading() :
          <div>
            <RandomGiphy
              isItRainingNow={rain.isItRainingNow}
              error={error}
            />
            {
              !error &&
              <div className="content">
                {
                  !backup &&
                  <div className="left">
                    <EmailInput />
                  </div>
                }
                <div className="right">
                  <Weather
                    backup={backup}
                    loading={loading}
                    forecast={{ daily, today }}
                    city="bontida"
                    background="#fee234"
                  />
                </div>
              </div>
            }
            <div className="donate">
              Daca ti-a placut, doneaza :)
              <div className="smallest">
              si daca nu ti-a placut, doneaza mai mult
              </div>
              <br />
              <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
                <input type="hidden" name="cmd" value="_s-xclick"/>
                <input type="hidden" name="encrypted" value="-----BEGIN PKCS7----------END PKCS7-----"/>
                <input type="image" src="https://www.paypalobjects.com/en_US/i/btn/btn_donateCC_LG.gif" border="0" name="submit" alt="PayPal - The safer, easier way to pay online!"/>
                <img alt="" border="0" src="https://www.paypalobjects.com/en_US/i/scr/pixel.gif" width="1" height="1" />
              </form>
            </div>
          </div>
        }
      </div>
    );
  }
}

export default App;



